import shijian from "@/assets/shijian.png";
import lianxikef from "@/assets/lianxikef.png";
import Clipboard from "clipboard";
import Copy from "@/assets/copy.png";
export default {
  data() {
    return {
      CopyIcon: Copy,
      shijian: shijian,
      lianxikef: lianxikef,
      showPositionValue: false, //提示信息
      toastContent: "",
      isWaiting: true,
      topTitle: "确认支付",
      disabled: false,
      order_id: this.$route.params.id,
      min: "00",
      second: "00",
      form: {
        pay_address: "",
        add_time: ""
      }
    };
  },
  beforeMount() {
    this.getData();
  },
  mounted() {},
  methods: {
    getTime() {
      var that = this,
        timer = setInterval(function() {
          var now = new Date();
          var end = new Date(that.form.add_time * 1000 + 30 * 60 * 1000);
          var result = parseInt(Math.floor(end - now) / 1000);
          if (result > 1) {
            result = result - 1;
            that.second = parseInt(Math.floor(result % 60)); // 计算秒 ，取余
            that.min = parseInt(Math.floor((result / 60) % 60));
          } else {
            clearInterval(timer);
            // that.disabled = true;
            that.isWaiting = false;
          }
        }, 1000);
    },
    getData() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          order_id: that.order_id
        };
      that
        .$request({
          method: "POST",
          url:"/order/order-detail",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.form = response.data;
          that.getTime();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    copyWord() {
      var that = this;
      var clipboard = new Clipboard(".CopyIcon");

      clipboard.on("success", function(e) {
        that.showPositionValue = true;
        that.toastContent = "复制成功";
        e.clearSelection();
      });

      clipboard.on("error", function(e) {
        console.error("Action:", e.action);
        console.error("Trigger:", e.trigger);
      });
    },
    //确认订单
    confirmOrder() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          order_id: that.order_id
        };
      that
        .$request({
          method: "POST",
          url:"/order/confirm-order",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          if (response.code==0) {
            that.showPositionValue = true;
            that.toastContent = "操作成功";
            setTimeout(function() {
             that.$router.push("/myOrder");
            }, 500);

          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    serviceInfo() {
      this.$router.push("/serviceInfo");
    }
  }
};



